.btn-basis(@bg-clr, @brd-clr, @txt-clr, @shd-clr){
    .frame;
    display: inline-block;
    font-weight: bold;
    padding: 0.7em;
    margin: 0.3em;
    border-width: 1px;
    border-style: solid;
    border-bottom-width: @bt-bord-width;
    text-decoration: none;
    border-color: @brd-clr;
    color: @txt-clr;
    background: @bg-clr;
    box-shadow: 0 1px 0px 0px @shd-clr inset;
    cursor: pointer;
    .transition();
    .noselect;
}

button, .btn{
   .btn-basis(#fff, @l-gray, @d-gray + #333, rgba(255, 255, 255, 0.5));
}
button:hover, .btn:hover{    
    .btn-basis(#fff, @l-gray - #222, @d-gray, rgba(255, 255, 255, 0.5));
}
button:active, .btn:active{  
    .btn-basis(@white - #222, @l-gray - #222, @d-gray, rgba(255, 255, 255, 0));  
    border-bottom-width: 1px;
    border-top-width: @bt-bord-width;
}
.btn-white{
    .btn;
    border-color: rgba(255, 255, 255, 0);
}

.btn-white{
   .btn-basis(#fff, rgba(255, 255, 255, 0), @d-gray + #333, rgba(255, 255, 255, 0));
}
.btn-white:hover{    
