.btn-group{
    display: table;
   /* padding: 0.1em 0.4em;*/
     -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;  
    *[class^=btn], button{
        margin: 0em;
        -webkit-border-radius: 0;
        -moz-border-radius: 0;
        padding: 1em 0;
        border-radius: 0;  
        border-collapse: collapse;
        width: 100%;
        text-align: center;
    }
    
    *[class^=btn]:first-child, button:first-child{
        .border-rad-4(@rad1:3px, @rad2:3px, @rad3:0, @rad4:0);
    }      
    *[class^=btn]:last-child, button:last-child{
        .border-rad-4(@rad1:0, @rad2:0, @rad3:3px, @rad4:3px);
    }
    
}